import { _decorator, Component, Node, find, AudioSource } from 'cc';
import { BaseLayer } from './framework/common/BaseLayer';
import { cocosUtil } from './framework/utils/CocosUtil';
import { layerManager } from './framework/manager/LayerManager';
import { audioManager } from './framework/manager/AudioManager';
import { constants } from './game/data/Constants';
import { msgcmd } from './game/data/MsgCmd';

const { ccclass, property } = _decorator;

@ccclass('GameLanuch')
export class GameLanuch extends BaseLayer {

    onLoad() {
        super.onLoad();

        cocosUtil.init()
        msgcmd.init();
        
        super.onLoad();

        let layer = find("Layer", this.node);
        let layerHint = find("LayerHint", this.node);
        layerManager.init(layer, layerHint);
        

        // 设置音频播放组件
        audioManager.setMusicAudioSource(this.getNodeByPath("Audio/Music").getComponent(AudioSource));
        audioManager.setEffectAudioSource(this.getNodeByPath("Audio/Effect").getComponent(AudioSource));
 

        // 添加LoadingLayer脚本
        let loadingName = constants.layers.LoadingLayer;




        this.getNodeByPath(loadingName).addComponent(loadingName);

        // let noticeName = constants.layers.NoticeLayer;
        // find(noticeName, layerHint).addComponent(noticeName);
    }

    start() {

    }

    update(deltaTime: number) {
        
    }
}

